@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
}

a {
  color: inherit;
  text-decoration: none;
}

.sec {
  width: 100%;
  padding: 70px 0 70px 0;
  scroll-margin-top: 50px;
}

.flexbox {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.sec h1 {
  background: rgba(0, 0, 0, 0);
  font-weight: 100;
  font-size: 1.3rem;
  text-align: center;
  margin-bottom: 70px;
  letter-spacing: 0.2em;
}
.Typist .Cursor {
    display: inline-block; }
    .Typist .Cursor--blinking {
      opacity: 1;
      -webkit-animation: blink 1s linear infinite;
              animation: blink 1s linear infinite; }
  
@-webkit-keyframes blink {
    0% {
      opacity: 1; }
    50% {
      opacity: 0; }
    100% {
      opacity: 1; } }
  
@keyframes blink {
    0% {
      opacity: 1; }
    50% {
      opacity: 0; }
    100% {
      opacity: 1; } }
.header {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    color: #3a8590;
    background: #dfdcd4;
    transition: all 0.2s ease-in-out;
}

.header2 {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    color: white;
    background: rgba(0, 0, 0, 0.4);
    transition: all 0.2s ease-in-out;
}

.container {
    max-width: 75rem;
    width: 100%;
}

.head {
    display: flex;
    justify-content: space-between;
}

.logo {
    margin-left: 2rem;
    padding: 0 0.3rem 0 0.3rem;
    font-size: 1.2rem;
    font-weight: 700;
    transition: all 0.2s ease-in-out;
}

.logo:hover {
    cursor: pointer;
}

.header .logo:hover {
    background-color: #3a8590;
    color: #dfdcd4;
}

.header2 .logo:hover {
    color: #fec5e5
}

.menu {
    text-align: center;
}

.menu ul {
    list-style-type: none;
    padding: 0;
}

.menu li {
    float: left;
    padding: 0 2rem 0 0;
    letter-spacing: 0.1em;
    font-weight: 300;
    transition: all 0.2s ease-in-out;
}

.menu li:hover {
    color: #000;
}

.header2 .menu li:hover {
    color: #fec5e5
}

@media screen and (max-width: 560px) {
    .header, .header2 {
        display: none;
    }
}
.main {
    min-height: 540px;
    text-align: center;
    padding: 50px 0 50px 0;
    background-color: #dfdcd4;
    color: #3a8590;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
}

.flex {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.typing {
    font-size: 2rem;
}

.typing span {
    font-size: 3rem;
    padding: 1.2rem 0 1rem 0;
    font-weight: 700;
}
.about {
    background: #fff;
    color: #000;
}

.maxw {
    max-width: 75rem;
    width: 100%;
}

.flex2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.pic {
    margin: 1rem;
}

.pic img {
    width: auto;
    max-width: 100%;
    transition: all 0.3s ease-in-out;
}

.pic img:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.intro {
    margin: 1rem;
    max-width: calc(75rem - 600px);
    flex: 1 1;
    min-width: 55%;
}

.iAlign {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    height: 100%;
}

.intro h2 {
    font-size: 1.7rem;
    padding-top: 2rem;
    text-align: center;
}

.intro p {
    font-size: 1.2rem;
}

.qLine {
    width: 100%;
}

.qLine:hover .quotes {
    font-size: 2.2rem;
}

.lAlign {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.quotes {
    color: #989898;
    font-size: 2rem;
    width: 2rem;
    padding: 1rem;
    transition: all 0.3s ease-in-out;
}

.qCon {
    width: 100%;
}

.qLine, .word {
    margin: 2rem 0 2rem 0;
}

@media screen and (max-width: 720px) {
    .qLine {
        text-overflow: hidden;
    }

    .intro h2 {
        font-size: 1.6rem;
    }
    
    .quotes {
        font-size: 1.5rem;
    }
}
.skill {
    background-color: #3a8590;
    color: #fff;
}

.maxw .sUl {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}


.liItem {
    font-size: 5rem;
    margin: 2rem;
    width: 7rem;
}

.liItem p {
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
    color: #fff;
}

.hov {
    background: #fff;
    color: #3a8590;
    padding: 1rem;
    text-align: center;
    border-radius: 0.5rem;
}

.sIcons {
    transition: all 0.2s ease-in-out;
}

.hov p {
    transition: all 0.2s ease-in-out;
}

.hov:hover .sIcons {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.hov:hover p {
    color: #3a8590;
}

.sName {
    font-size: 1rem;
}
.project {
    background: #b5d2ce;
    color: #000;
}

.frame {
    max-width: 380px;
    background-color: #fff;
    box-shadow: 0 4px 6px rgb(0, 0, 0, 0.3);
    transition: box-shadow 0.3s ease-in-out;
    padding: 1.3rem 0 1.3rem 0;
    margin: 2rem;
    overflow: hidden;
}

.frame:hover {
    box-shadow: 0 7px 15px rgba(0, 0, 0, 0.6);
}

.fflex {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
}

.des {
    color: #000;
    width: 100%;
    margin: 0 1.2rem 0 1.2rem;
}

.des p {
    font-size: 0.9rem;
}

.pjs {
    margin: 1rem;
}

.pjs img {
    width: auto;
    max-width: 100%;
}

h2 {
    margin-bottom: 1rem;
}
.contact {
    background: #fff;
    color: #000;
}

.cIcons {
    display: flex;
    flex-direction: column;
}

.iFlex:hover {
    color: #3a8590;
    border-color: #3a8590;
}

.ci {
    font-size: 3rem;
    position: relative; 
    left: -3px;
}

.addr {
    font-size: 1.2rem;
    max-height: 4rem;
    padding: 0 1rem 0 1rem;
}

.iFlex {
    border: 3px solid black;
    max-height: 48px;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    margin: 1.5rem;
    transition: all 0.3s ease;
}

@media screen and (max-width: 720px) {
    .addr {
        display: none;
    }

    .ci {
        left: 0px;
        font-size: 6rem;
    }

    .iFlex {
        border: none;
        max-height: 100%;
    }
}
footer {
    background-color: #dfdcd4;
    color: #3a8590;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

footer p {
    font-size: 0.9rem;
}
